import numImgPair from './imgComponents/numImgPair.vue'
import numImgSelect from './imgComponents/numImgSelect.vue'
import numImgCount from './imgComponents/numImgCount.vue'
import numImgSort from './imgComponents/numImgSort.vue'
export default {
	components: {
		numImgPair,
		numImgSelect,
		numImgCount,
		numImgSort
	},
	data() {
		return {
			allData:{},
			problemList: [], //保存所有已选中的数据
			showH5: true,
			topLeftName: '数图配对',
			btnName: '下一组',
			page:{
				pageNum: 0, // 当前组数下标
				examNum: 10,
				examTime: null,
			},
			userExamTime: 0, //用户选择的答题时间数
			optionInfo:{
				collectsId: '402aa38151aef50c0151aef50c2600cc',
				time: 0,
				type: 1,
				num: 8
			},// 所有从前一个页面传过来的数据（需要传给后台的数据）
		};
	},
	onLoad(options){
		if(options){
			this.optionInfo = JSON.parse(options.options)
			console.log(this.optionInfo)
		}
	},
	onShow(){
		if(navigator){
			this.showH5 = true
		}else{
			this.showH5 = false
		}
		this.startNumExamination()
	},
	methods:{
		// 获取训练数据列表
		startNumExamination(){
			if(uni.getStorageSync('userInfo')){
				let memberId = JSON.parse(uni.getStorageSync('userInfo')).id
				this.$Request.get('/appExaminationController.do?startNumExamination',{
					...this.optionInfo,
					memberId:memberId
				}).then(res => {
					if(res.code == 0){
						this.allData = res.data
						this.userExamTime = res.data.examTime == 0 ? '' : res.data.examTime
						this.page.examTime = this.userExamTime // 用户选择的每题时间数
						this.page.examNum = this.allData.examNum // 测试组数
						if(this.userExamTime != ''){
							const timer = setInterval(() => {
								// 倒计时
								this.page.examTime = this.page.examTime - 1;
								// 当倒计时为0时
								if (this.page.examTime === 0) {
									console.log('allData', this.allData)
									if(Number(this.page.pageNum)+ 1 == Number(this.page.examNum)){
										console.log('所有题都答完了')
										console.log('onshow',this.problemList)
										this.btnName = '结束答题'
										clearInterval(timer);
										return
									}else{
										this.problemList.push({
											answerId: '￥',
											problemId: '￥',
											problemName: '',
											problemPic: ''
										})
										this.page.pageNum += 1
										this.page.examTime = this.userExamTime
									}
								}
							}, 1000);
						}
					}else{
						uni.showToast({
							title: res.info,
							icon: 'none'
						})
					}
				})
			}else{
				uni.showToast({
					title: '您尚未登录，正在跳往登录页面。。。',
					icon: 'none'
				})
				setTimeout(() => {
					uni.navigateTo({
						url:'/pages/loginAll/login'
					})
				}, 1000)
			}
		},
		clickProblemList(arr){
			this.problemList = arr
		},
		clickNext(){
			if(Number(this.page.pageNum)+ 1 == Number(this.page.examNum)){
				console.log('所有题都答完了')
				console.log('btn',this.problemList)
				this.btnName = '结束答题'
				return
			}else{
				this.problemList.push({
					answerId: '￥',
					problemId: '￥',
					problemName: '',
					problemPic: '',
				})
				this.page.pageNum += 1
			}
		},
		// 答题结束
		takeNumExamination(){
			this.$Request.postJson('/appExaminationController.do?takeNumExamination',{
				...this.answerInfo
			}).then(res => {
				if(res.code == 0){
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
	}
}
